.form-control {
  margin: 1rem 0;
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  input,
  textarea {
    display: block;
    width: 100%;
    font: inherit;
    border: 1px solid #ccc;
    background: #f8f8f8;
    padding: 0.15rem 0.25rem;
    &:focus {
      outline: none;
      background: #ebebeb;
      border-color: #510077;
    }
  }
  &--invalid {
    label,
    p {
      color: red;
    }
    input,
    textarea {
      border-color: red;
      background: #ffd1d1;
    }
  }
}
