.main-navigation {
  &__menu-btn {
    width: 3rem;
    height: 3rem;
    background: transparent;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-right: 2rem;
    cursor: pointer;
    span {
      display: block;
      width: 3rem;
      height: 2.5px;
      background: white;
    }
  }
  &__title {
    color: white;
    a {
      text-decoration: none;
      color: white;
    }
  }
  &__header-nav {
    display: none;
  }
  &__drawer-nav {
    height: 100%;
  }
}

@media (min-width: 768px) {
  .main-navigation {
    &__menu-btn {
      display: none;
    }
    &__header-nav {
      display: block;
    }
  }
}
