@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box; }

html {
  font-family: 'Open Sans', sans-serif; }

body {
  margin: 0;
  padding: 0 3rem; }

.slide-in-left-enter {
  transform: translateX(-100%); }
  .slide-in-left-enter-active {
    transform: translateX(0);
    opacity: 1;
    transition: all 200ms; }

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1; }
  .slide-in-left-exit-active {
    transform: translateX(-100%);
    opacity: 0;
    transition: all 200ms; }

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center; }

.users-list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 90%;
  max-width: 50rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.avatar {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .avatar img {
    display: block;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover; }

.card {
  position: relative;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: white; }

.user-item {
  margin: 1rem;
  width: calc(45% - 2rem);
  min-width: 17.5rem; }
  .user-item a {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    text-decoration: none;
    padding: 1rem;
    color: white;
    background: #292929; }
    .user-item a:hover, .user-item a:active {
      background: #ffd900; }
  .user-item__content {
    padding: 0; }
  .user-item__image {
    width: 4rem;
    height: 4rem;
    margin-right: 1rem; }
  .user-item__info h2 {
    font-size: 1.5rem;
    margin: 0 0 0.5rem 0;
    font-weight: normal;
    color: #ffd900; }
  .user-item__info h3 {
    margin: 0; }
  .user-item:hover h2,
  .user-item:hover h3, .user-item:active h2,
  .user-item:active h3 {
    color: #292929; }

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10; }

.modal {
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px; }
  .modal__header {
    width: 100%;
    padding: 1rem 0.5rem;
    background: #2a006e;
    color: white; }
    .modal__header h2 {
      margin: 0.5rem; }
  .modal__content {
    padding: 1rem 0.5rem; }
  .modal__footer {
    padding: 1rem 0.5rem; }
  .modal-enter {
    transform: translateY(-10rem);
    opacity: 0; }
    .modal-enter-active {
      transform: translateY(0);
      opacity: 1;
      transition: all 200ms; }
  .modal-exit {
    transform: translateY(0);
    opacity: 1; }
    .modal-exit-active {
      transform: translateY(-10rem);
      opacity: 0;
      transition: all 200ms; }

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem; } }

.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ff0055;
  border-radius: 4px;
  background: #ff0055;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block; }
  .button:focus {
    outline: none; }
  .button:hover, .button:active {
    background: #ff4382;
    border-color: #ff4382; }
  .button--inverse {
    background: transparent;
    color: #ff0055; }
    .button--inverse:hover, .button--inverse:active {
      color: white;
      background: #ff0055; }
      .button--inverse:hover:disabled, .button--inverse:active:disabled {
        background: #ccc;
        color: #979797;
        border-color: #ccc;
        cursor: not-allowed; }
  .button--danger {
    background: #830000;
    border-color: #830000; }
    .button--danger:hover, .button--danger:active {
      background: #f34343;
      border-color: #f34343; }
  .button:disabled {
    background: #ccc;
    color: #979797;
    border-color: #ccc;
    cursor: not-allowed; }
  .button--small {
    font-size: 0.8rem; }
  .button--big {
    font-size: 1.5rem; }

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px; }
  .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 46px;
    height: 46px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid #510077;
    border-color: #510077 transparent #510077 transparent;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite; }

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center; }

@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.form-control {
  margin: 1rem 0; }
  .form-control label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem; }
  .form-control input,
  .form-control textarea {
    display: block;
    width: 100%;
    font: inherit;
    border: 1px solid #ccc;
    background: #f8f8f8;
    padding: 0.15rem 0.25rem; }
    .form-control input:focus,
    .form-control textarea:focus {
      outline: none;
      background: #ebebeb;
      border-color: #510077; }
  .form-control--invalid label,
  .form-control--invalid p {
    color: red; }
  .form-control--invalid input,
  .form-control--invalid textarea {
    border-color: red;
    background: #ffd1d1; }

.image-upload.center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.image-upload__preview {
  width: 13rem;
  height: 13rem;
  border: 1px solid #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 1rem; }
  .image-upload__preview.img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

.place-form {
  list-style: none;
  margin: 0 auto;
  padding: 1rem;
  width: 90%;
  max-width: 40rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  background: white; }

.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #00ff72;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
  justify-content: space-between; }

main {
  margin-top: 5rem; }

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .nav-links li {
    margin: 1rem; }
  .nav-links a {
    border: 1px solid transparent;
    color: #292929;
    text-decoration: none;
    padding: 0.5rem; }
    .nav-links a:hover, .nav-links a:active, .nav-links a.active {
      background: #f8df00;
      border-color: #292929;
      color: #292929; }
  .nav-links button {
    cursor: pointer;
    border: 1px solid transparent;
    color: #292929;
    background: transparent;
    padding: 0.5rem;
    font: inherit; }
    .nav-links button:hover, .nav-links button:active, .nav-links button.active {
      background: #f8df00;
      border-color: #292929;
      color: #292929; }
    .nav-links button:focus {
      outline: none; }

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row; }
    .nav-links li {
      margin: 0 0.5rem; }
    .nav-links a {
      color: white;
      text-decoration: none; }
    .nav-links button {
      border: 1px solid transparent;
      color: white;
      background: transparent; }
      .nav-links button:hover, .nav-links button:active {
        background: #f8df00;
        border-color: #292929;
        color: #292929; } }

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26); }

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer; }
  .main-navigation__menu-btn span {
    display: block;
    width: 3rem;
    height: 2.5px;
    background: white; }

.main-navigation__title {
  color: white; }
  .main-navigation__title a {
    text-decoration: none;
    color: white; }

.main-navigation__header-nav {
  display: none; }

.main-navigation__drawer-nav {
  height: 100%; }

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none; }
  .main-navigation__header-nav {
    display: block; } }

.place-list {
  list-style: none;
  margin: 1rem auto;
  padding: 0;
  width: 90%;
  max-width: 40rem; }

.place-item {
  margin: 1rem 0; }
  .place-item__content {
    padding: 0; }
  .place-item__info {
    padding: 1rem;
    text-align: center; }
    .place-item__info h2,
    .place-item__info h3,
    .place-item__info p {
      margin: 0 0 0.5rem 0; }
  .place-item__image {
    width: 100%;
    height: 12.5rem;
    margin-right: 1.5rem; }
    .place-item__image img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .place-item__actions {
    padding: 1rem;
    text-align: center;
    border-top: 1px solid #ccc; }
    .place-item__actions button,
    .place-item__actions a {
      margin: 0.5rem; }
  .place-item__modal-content {
    padding: 0; }
  .place-item__modal-actions {
    text-align: right; }

.map-container {
  height: 15rem;
  width: 100%; }

@media (min-width: 768px) {
  .place-item__image {
    height: 20rem; } }

.map {
  width: 100%;
  height: 100%; }

.authentication {
  width: 90%;
  max-width: 25rem;
  margin: 7rem auto;
  text-align: center; }
  .authentication__header {
    color: white;
    text-align: center; }
  .authentication form {
    margin-bottom: 1rem; }

